
import { defineComponent, watch } from "vue";

export default defineComponent({
  name: "AppInputCounter",
  props: {
    inputLength: {
      type: Number,
      required: true,
    },
    maxLength: {
      type: Number,
      required: true,
    },
  },
  emits: ["tooLongText"],
  setup(props, { emit }) {
    watch(
      () => props.inputLength,
      (newInputLength) => {
        if (newInputLength > props.maxLength) {
          emit("tooLongText");
        }
      }
    );
  },
});
