
import { defineComponent } from "vue";

export default defineComponent({
  name: "SlidingInput",
  props: {
    label: {
      type: String,
      required: true,
    },
    modelValue: {
      type: String,
    },
  },
  setup(props, { emit }) {
    const updateValue = (event: any) => {
      emit("update:modelValue", event.target.value);
    };

    return { updateValue };
  },
});
